 #fff;\n    z-index: 2;\n  }\n  &.green {\n    .canvas-item-icon {\n      color: @green;\n    }\n    .canvas-header {\n      span {\n        background: @green;\n      }\n    }\n    .canvas-list-number {\n      background: lighten(@green, 10%);\n    }\n  }\n  &.orange {\n    .canvas-item-icon {\n      color: @orange;\n    }\n    .canvas-header {\n      span {\n        background: @orange;\n      }\n    }\n    .canvas-list-number {\n      background: lighten(@orange, 10%);\n    }\n  }\n  &.blue {\n    .canvas-item-icon {\n      color: @blue;\n    }\n    .canvas-header {\n      span {\n        background: @blue;\n      }\n    }\n    .canvas-list-number {\n      background: lighten(@blue, 10%);\n    }\n  }\n  &.pink {\n    .canvas-item-icon {\n      color: @pink;\n    }\n    .canvas-header {\n      span {\n        background: @pink;\n      }\n    }\n    .canvas-list-number {\n      background: lighten(@pink, 10%);\n    }\n  }\n  &.yellow {\n    .canvas-item-icon {\n      color: @yellow;\n    }\n    .canvas-header {\n      span {\n        background: @yellow;\n      }\n    }\n    .canvas-list-number {\n      background: lighten(@yellow, 10%);\n    }\n  }\n  &.red {\n    .canvas-item-icon {\n      color: @red;\n    }\n    .canvas-header {\n      span {\n        background: @red;\n      }\n    }\n    .canvas-list-number {\n      background: lighten(@red, 10%);\n    }\n  }\n  .canvas-list-number {\n    background: lighten(@mainColor, 10%);\n  }\n  .canvas-item-icon {\n    color: @mainColor;\n  }\n  .canvas-item-icon {\n    position: absolute;\n    right: 0;\n    top: 0;\n    height: 0;\n    font-size: 5vw;\n    margin: -2rem 0;\n    opacity: 0.1;\n    transform: rotate(10deg);\n  }\n  .canvas-header {\n    font-size: 0.5em;\n    text-transform: uppercase;\n    letter-spacing: 0.15rem;\n    flex: 0 0 auto;\n    font-weight: 600;\n    span {\n      background: @mainColor;\n      color: @canvasHeaderColor;\n      display: inline-block;\n      border-radius: 3px 0 3px;\n    }\n  }\n  .canvas-item-content,\n  .canvas-item-details {\n    backface-visibility: hidden;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    flex: 1 1 100%;\n    display: flex;\n    flex-direction: column;\n  }\n  .canvas-item-content {\n    position: relative;\n  }\n  .canvas-item-details {\n    position: absolute;\n  }\n  .canvas-item-details {\n    display: none;\n    background: #f0f0f0;\n    color: #333;\n    font-size: 0.9em;\n    h3,\n    h4 {\n      font-size: 1em;\n      font-weight: 800;\n    }\n    h3,\n    h4,\n    p {\n      margin-bottom: 1rem;\n    }\n    .canvas-details-close {\n      display: none;\n    }\n  }\n  .canvas-content {\n    flex: 1 1 100%;\n    display: flex;\n    flex-direction: column;\n    .canvas-field {\n      flex: 1;\n    }\n    .canvas-field-header {\n      font-size: 0.6em;\n      font-weight: 600;\n      text-transform: uppercase;\n      margin: 0;\n      padding-top: 0.5em;\n      margin-bottom: 0.5em;\n      color: #666;\n      padding-bottom: 0.5em;\n      box-sizing: border-box;\n      border-bottom: 1px solid @borderColor;\n      span {\n        font-weight: 300;\n      }\n      .canvas-list-number {\n        font-weight: 800;\n      }\n    }\n    .canvas-item-tip,\n    .canvas-field-tip {\n      margin: 0 0 0.5rem;\n      color: #999;\n      font-style: italic;\n    }\n    .canvas-item-tip {\n      font-size: 0.8em;\n    }\n    .canvas-field-tip {\n      font-size: 0.7em;\n    }\n  }\n  .canvas-item-details-button {\n    display: none;\n  }\n}\n\n.canvas-field-group {\n  display: flex;\n  flex-direction: row;\n  flex-grow: 1;\n  flex: 1;\n}\n\n.canvas-item-group {\n  display: flex;\n  flex-direction: column;\n  flex-grow: 1;\n  .canvas-item-group-header {\n    margin: 0.5rem;\n    .canvas-item-group-title {\n      margin: 0;\n      font-size: 1em;\n    }\n    .canvas-item-group-description {\n      font-size: 0.8em;\n      p {\n        margin: 0;\n      }\n    }\n  }\n  .canvas-item-group-content {\n    display: flex;\n    flex-grow: 1;\n  }\n}\n\n.canvas-list {\n  display: flex;\n  flex: 1 1 100%;\n  flex-direction: column;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  .canvas-list-item {\n    flex: 1 1 100%;\n    margin: 0;\n    padding: 0;\n  }\n  .canvas-list-number {\n    font-size: 1em;\n    width: 1.5em;\n    height: 1.5em;\n    line-height: 1.5em;\n    display: inline-block;\n    color: #fff;\n    border-radius: 100%;\n    text-align: center;\n    float: right;\n    font-weight: 800;\n  }\n}\n\n.canvas-table {\n  width: 100%;\n  height: 100%;\n  table-layout: fixed;\n  box-sizing: border-box;\n  margin: 0;\n  th,\n  td {\n    border-right: 1px solid @borderColor;\n  }\n  th {\n    padding: 0.5em 1em;\n    border-bottom: 1px solid @borderColor;\n    height: 21px;\n    color: #666;\n  }\n  td {\n    border-bottom: 0;\n    padding: 0.5em;\n    vertical-align: top;\n  }\n  th:last-child,\n  td:last-child {\n    border-right: 0;\n  }\n  &.numbered {\n    td {\n      height: 1px;\n      border-bottom: 1px solid @borderColor;\n    }\n    tr:last-child td {\n      border-bottom: 0;\n      height: auto;\n    }\n  }\n}\n\n@media only screen (min-width: 801px) and (max-width: 1000px) {\n  .canvas-container {\n    font-size: 0.8em;\n  }\n}\n\n@media only screen (min-width: 701px) and (max-width: 800px) {\n  .canvas-container {\n    font-size: 0.7em;\n  }\n}\n","@import \"global\";\n@import \"screen\" only screen;\n@import \"mobile\" only screen and (max-width: 700px);\n@import \"print\"  only print;\n",".canvas-container {\n  padding: 0.5rem;\n}\n\n.canvas-row.colored {\n  padding: 0.5rem;\n  margin-bottom: 0.5rem;\n}\n\n.canvas-row.attached {\n  margin-left: -0.5rem;\n  margin-right: -0.5rem;\n  &.top {\n    margin-top: -0.5rem;\n  }\n}\n\n.canvas-item {\n  margin: 0.5rem;\n  transition: all ease-in-out 0.1s;\n  cursor: default;\n  overflow: hidden;\n  box-shadow: 0 0 0 rgba(0, 0, 0, 0.1);\n  // transition: 0.6s;\n  transform-style: preserve-3d;\n  overflow-y: auto;\n  overflow-x: hidden;\n  &.featured {\n    box-shadow: inset 0 0 0 0.75rem rgba(0, 0, 0, 0.02);\n  }\n  .canvas-item-icon {\n    transition: all ease-in-out 0.2s;\n  }\n  &.has-details {\n    cursor: pointer;\n  }\n  &.empty {\n    overflow: hidden;\n  }\n  .canvas-item-content,\n  .canvas-item-details {\n    padding: 1.5rem;\n    display: flex;\n    transition: 0.6s;\n    transform-style: preserve-3d;\n  }\n  .canvas-item-content {\n    z-index: 2;\n    background: #fff;\n  }\n  .canvas-item-details {\n    transform: rotateY(180deg);\n    background: #333;\n    color: #f0f0f0;\n    .canvas-details-close {\n      display: block;\n      position: absolute;\n      line-height: 1;\n      text-transform: lowercase;\n      text-decoration: none;\n      color: #fff;\n      font-weight: 600;\n      top: 0;\n      right: 0;\n      padding: .7rem 1rem;\n      background: #000;\n      border-radius: 0 4px 0 4px;\n      z-index: 2;\n      &:hover {\n        color: #999;\n      }\n    }\n    .canvas-details {\n      position: absolute;\n      cursor: default;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      top: 0;\n      overflow: auto;\n      padding: 1.5rem 3rem 1.5rem 1.5rem;\n    }\n  }\n  &.active {\n    box-shadow: 0 0 0 !important;\n    .canvas-item-content {\n      transform: rotateY(180deg);\n      z-index: auto;\n    }\n    .canvas-item-details {\n      transform: rotateY(0deg);\n    }\n  }\n  .canvas-item-details-overlay {\n    position: absolute;\n    z-index: 2;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: all linear 0.2s;\n  }\n  &:hover {\n    z-index: 3;\n    background: #fff;\n    box-shadow: 0 0 4rem rgba(0, 0, 0, 0.1);\n    border-color: transparent;\n    .canvas-item-icon {\n      transform: rotate(-5deg);\n    }\n    .canvas-item-details-overlay {\n      background: rgba(255, 255, 255, 0.6);\n    }\n    .canvas-item-details-button {\n      display: inline-block;\n      font-size: 0.6em;\n      letter-spacing: 0.1rem;\n      text-transform: uppercase;\n      background: #fff;\n      box-shadow: 0 0 2rem rgba(0, 0, 0, 0.1);\n      border-radius: 3px;\n      padding: 1rem 1.5rem;\n      z-index: 2;\n      color: #000;\n      text-decoration: none;\n      border: 1px solid @borderColor;\n    }\n  }\n  .canvas-header {\n    margin: 0 0 1rem;\n    margin: -1.5rem -1.5rem 0.75rem;\n    span {\n      padding: 0.75rem 1.5rem;\n    }\n  }\n  .canvas-content.attached {\n    margin-left: -1.5rem;\n    margin-right: -1.5rem;\n    margin-bottom: -1.5rem;\n  }\n  .canvas-item-details-container {\n    position: fixed;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    z-index: 3;\n    background: rgba(255, 255, 255, 0.8);\n    .canvas-item-details {\n      max-width: 800px;\n      margin-top: 10%;\n      background: #fff;\n      border-radius: 3px;\n      box-shadow: 0 0 1px @borderColor, 0 0 2rem rgba(0, 0, 0, 0.1);\n      padding: 2rem;\n    }\n  }\n}\n.canvas-field-group {\n  margin-bottom: 0.5rem;\n}\n",".canvas-container,\n.canvas-row,\n.canvas-column,\n.canvas-item,\n.canvas-item-group,\n.canvas-item-group .canvas-item-group-content,\n.canvas-item .canvas-item-content,\n.canvas-item .canvas-header,\n.canvas-item .canvas-content {\n  display: block;\n}\n\n.canvas-item {\n  margin-bottom: 1rem;\n  .canvas-item-icon {\n    font-size: 8vh;\n  }\n  .canvas-content {\n    min-height: 4em;\n    .canvas-table {\n      tbody td {\n        padding-top: 4em;\n      }\n    }\n  }\n}\n","@minContentSize: 1cm;\n\n.canvas-container {\n  padding: 0.1cm;\n}\n\n.canvas-row.colored {\n  padding: 0.1cm;\n  margin-bottom: 0.1cm;\n}\n\n.canvas-row.attached {\n  margin-left: -0.1cm;\n  margin-right: -0.1cm;\n  &.top {\n    margin-top: -0.1cm;\n  }\n}\n\n.canvas-item {\n  margin: 0.1cm;\n  &.featured {\n    box-shadow: inset 0 0 0 0.125cm rgba(0,0,0,0.02);\n  }\n  .canvas-item-icon {\n    font-size: 1.25cm;\n  }\n  .canvas-h